.segment {
  margin-top: 30px;
  margin-bottom: 30px;
  border-top: 1px solid var(--secondary);
  text-align: center;
}

.segment__title {
  display: inline-block;
  margin: 0;
  padding: 0 5px;

  font-size: 16px;
  background-color: var(--offset);
  transform: translateY(-14px);
}

.segment__glyph-container {
  display: flex;
  flex-wrap: wrap;
}

.segment__glyph {
  width: 200px;
  height: 60px;
  margin: 20px;

  font-size: 25px;
  color: var(--secondary);
  background-color: transparent;
  outline: none;
  border: none;
  border-radius: 8px;
  cursor: pointer;

  transition: background-color 0.1s ease, color 0.1s ease;
}

.segment__glyph:hover {
  background-color: var(--primary);
  color: var(--offset);
}
